body {
    font-family: Arial, sans-serif;
    margin: 20px;
  }
  
  .app {
    max-width: 400px;
    margin: 20px auto;
    text-align: center;
  }
  
  .task-form input {
    padding: 8px;
    width: 70%;
    margin-right: 10px;
  }
  
  .task-form button {
    padding: 8px;
    background-color: #4caf50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .task-list {
    list-style: none;
    padding: 0;
  }
  
  .task-list li {
    display: flex;
    justify-content: space-between;
    background: #f4f4f4;
    margin-bottom: 5px;
    padding: 10px;
  }
  
  .task-list li.completed span {
    text-decoration: line-through;
    color: gray;
  }
  
  .task-list button {
    background-color: #f44336;
    color: white;
    border: none;
    cursor: pointer;
  }
  